import React from 'react'
import { connect } from 'react-redux'

import View from '../view'

export const SearchPeopleWrapper = (
  {
    user,
    choosenUserId,

    onClick
  }) => {
  
  // Все это.. можно и нужно перенести в редюсер
  
  const showTarget = user

  const isChoosen = user['_id'] === choosenUserId;

  const showData = {
    imgPlug: showTarget.nickName[0],
    name: showTarget.nickName,
    clickHandler: () => !isChoosen && onClick(showTarget['_id']),
    online: showTarget.online,
    isSearchPeopleMod: true,
    isChoosen
  }

  return (
    <View {...showData} />
  )
}

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(SearchPeopleWrapper)
