import React, { useEffect } from 'react';
import { 
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  } from 'react-router-dom'
import isMobile from 'ismobilejs';
import { connect } from 'react-redux'


import { 
  PreviewPage, 
  ResultPage, 
  ChoosePage, 
  TestPage, 
  LoginPage, 
  MessengerPage,
  MePage,
  ChatPage,

} from '@@/pages'

import './styles/index.scss';

const App = ({ isMobileVersion }) => {

  useEffect(() => {
    const { pathname, replace } = window.location;
    if(isMobile(window.navigator).phone && isMobileVersion) {
      replace(pathname);
    }
  })

  return (
    <div className='pageWrapper'>
      <Router>
        <Switch>
          <Route exact path="/">
            <Redirect to='/about' />
          </Route>
          <Route exact path="/about">
            <PreviewPage />
          </Route>
          <Route exact path="/result">
            <ResultPage />
          </Route>
          <Route path="/main">
            <ChoosePage />
          </Route>
          <Route exact path="/dude/*">
            <div>{"hey dude"}</div>
          </Route>
          <Route exact path="/test">
            <TestPage />
          </Route>
          <Route exact path='/login'>
            <LoginPage />
          </Route>
          <Route exact path='/messenger'>
            <MessengerPage />
          </Route>
          <Route exact path='/me'>
            <MePage />
          </Route>
          <Route exact path='/chat'>
            <ChatPage />
          </Route>
        </Switch>
      </Router>
    </div>
  )
}

export default connect(({dialogs: {isMobileVersion}}) => ({isMobileVersion}))(App) ;