import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'

import { LeftOutlined } from '@ant-design/icons';

import './ChatHeader.scss'

export const ChatHeader = ({dialogs, choosenDialogId, user, v}) => {
  const history = useHistory();

  const [personData, setPersonData] = useState({nickName: '', lastSeen: '', lastV: ''})
  useEffect(() => {
    // Обработка каких либо изменений и обновление данных в шапке

    const keyWord = v.split('$$')[0];
    if(v !== personData.lastV) {
      switch(keyWord) {
        case 'pickDialog':
        case 'setProfile':
        case 'setOnline': 
        case 'pickEvent':
        (() => {
          let i = -1;
          while(++i < dialogs.length) {
            
            if(dialogs[i].dialog['_id'] === choosenDialogId) {
              const { author, partner } = dialogs[i].dialog
              const partnerData = author['_id'] === user['_id'] ? partner : author;
              const { nickName, online } = partnerData;

              setPersonData({
                nickName, 
                lastSeen: online ? 'online' : 'offline', 
                lastV: v
              })
            }
          }

          
        })()
        break
        default:
        break
      }
    }
    
  })

  const { nickName, lastSeen } = personData
  return (
    <section className={'chatHeader'}>
      <div className='chatHeader__leftPart' onClick={() => history.push('/messenger')}>
        <LeftOutlined />
      </div>
      <div className='chatHeader__rightPart'>
        <div className='chatHeader__rightPart_partnerName'>
          {nickName}
        </div>
        <div className='chatHeader__rightPart_lastSeenDate'>
          {lastSeen}
        </div>
      </div>
    </section>
  )
}

const mapStateToProps = ({dialogs: {dialogs, choosenDialogId, user, v}}) => ({
  dialogs, choosenDialogId, user, v
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(ChatHeader)
