import React from 'react'
import { connect } from 'react-redux'
import { Redirect, useLocation } from 'react-router-dom'

import { DialogsTab, DialogsSearch } from './layout'
import { MenuButton } from '@@/components'

import './MessangerPage.scss'

export const MessangerPage = ({isAuth}) => {
  const location = useLocation()

  return (
    <>
      <section className='messangerPage'>
        <div className='messangerPage_header'>
          <div className='messangerPage_header__return'>
            <MenuButton />
          </div>
          <div className='messangerPage_header__search'>
            <DialogsSearch />
          </div>
        </div>
        <div className='messangerPage_dialogs'>
          <DialogsTab />
        </div>
      </section>
      {!isAuth && <Redirect to={{pathname: "/login", state: { from: location.pathname}}} />}
    </>
  )
}

const mapStateToProps = ({dialogs: {isAuth}}) => ({
  isAuth
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(MessangerPage)
