import React, { useRef, useEffect, useState } from 'react'

import './TextReducer.scss'

 const TextReducer = ({text}) => {
  const wrapperRef = useRef(null);
  const targetRef = useRef(null);
  const [originalText, setOriginalText] = useState(null);
  const [textData, setTextData] = useState({
    payload: text,
    ready: false, 
    changed: false,
  })

  useEffect(() => {
    if(originalText !== text) { // ручное обновление главного пропса
      setOriginalText(text);
      setTextData({payload: text, ready: false, changed: false})
    }

    const { clientHeight: wrapperH } = wrapperRef.current,
    { clientHeight: targetH } = targetRef.current,
    {payload, ready, changed} = textData;

    if(targetH > wrapperH) {
      let resStr = payload.substring(0, 0.85*payload.length/(targetH/wrapperH));
      setTextData({...textData, payload: resStr, changed: true })
    } else if(!ready) {
      let textWithSpace = payload
      textWithSpace = changed 
        ? `${textWithSpace.substring(0, payload.length-3)}...`
        : textWithSpace;
      setTextData({...textData, payload:textWithSpace, ready: true})
    }

  })



  return (
    <div ref={wrapperRef} className='textReducer'>
      <div ref={targetRef} className='textReducer__target'>
        {textData.payload}
      </div>
    </div>
  )
}

export default TextReducer