import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'

import { Bread } from '@@/components'

import './Header.scss'

const CPHeader = props => {
  return (
    <section className={'cPHeader'}>
      <div className={'cPHeader_rightSpace'} />
        <div className={'cPHeader__main'}>
          <Bread />
        </div>
        <div className={'cPHeader_rightSpace'}>
          <Link to={'/result'} >{"Result"}</Link>
        </div>
    </section>
  )
}

CPHeader.propTypes = {

}

export default CPHeader
