// STATE.V всегда должен быть новым, а иначе старые действия повторяться... 

import { 
  PRIVATE_MSG, 
  JOIN_TO_ROOM, 
  GET_MSGS, 
  INIT,
  PICK_DIALOG,
  SET_DIALOG_TAB_MOD,
  SET_SEARCH_PEOPLE,
  CREATE_NEW_DIALOG,
  CREATE_MESSAGE,
  SET_MESSAGE,
  SET_DIALOG,
  JOIN_ROOM,
  READ_MESSAGES,
  SET_READ_MESSAGES,
  GET_POLL_MESSAGES,
  SET_V,
  SET_POLL_MESSAGES,
  EDIT_PROFILE, 
  SET_PROFILE,
  SET_MY_PROFILE,
  SET_ONLINE,
  LOGOUT,
  ADD_QUERY,
  UNSET_DIALOG_ID
} from '@/constants/ActionTypes'

const defaultStatus = {
  room: '',
  lastMessage: {addres: '', message: ''},
  v: '',
  dialogs: [],
  user: null,
  isAuth: false,
  choosenDialogId: null,
  readyMessengesData: [],
  dialogTabMod: 'base',
  searchPeople: [],
  newDialogTargetId: null,
  messageData: {},
  isLiveUnreaded: false,
  readMessagesPass: '',
  newProfileData: {},
  logOutId: '',
  showSearchPeople: [],
  isMobileVersion: true,
}

export default (state = defaultStatus, {type, payload}) => {
  switch(type) {
    case PRIVATE_MSG: 
      return (() => {
        const {msg, addres} = payload;

        state.lastMessage = {addres, message: msg}
        state.v = getV('m')
        
        return state
      })()
    case JOIN_TO_ROOM:  
      return (() => {
        const {room} = payload;

        state.room = room;
        state.v = getV('r')
        
        return state 
      })()
    case GET_MSGS: // в отдельный сокетовый редюсер
      return (() => {
        state.v = getV('g')

        return state
      })()
    // case JOIN_ROOM:
    //   return (() => {
    //     debugger
    //     

    //     state.newDialogTargetId = userId
    //     state.v = getV('joinRoom')
    //     return state
    //   })()
    case CREATE_NEW_DIALOG: // в отдельный сокетовый редюсер
      // может вызваться по персонажу с существующим диалогом
      return (() => {
        const { userId } = payload

        state.newDialogTargetId = userId // служебка
        state.v = getV('createDialog')
        return state;
      })()
    case SET_DIALOG: 
      return (() => {
        const { dialog } = payload

        state.dialogs.push({
          dialog,
          messages: []
        })

        state.v = getV()
        return state
      })()
    case CREATE_MESSAGE:
      return (() => {
        const { text } = payload;
        
        const dialog = (() => {
          let dialogInd = -1;
          let i = -1;
          while(++i < state.dialogs.length) {
            if(state.choosenDialogId === state.dialogs[i].dialog['_id']) {
              dialogInd = i;
              break
            }
          }
          return state.dialogs[dialogInd].dialog
        })()

        state.messageData = {
          poll: dialog.poll,
          dialogId: dialog['_id'],
          userId: state.user['_id'],
          targetUserId: dialog.author['_id'] === state.user['_id'] 
            ? dialog.partner['_id'] : dialog.author['_id'],
          text
        }
        state.v = getV('createMessage')
        return state;
      })()
    case SET_MESSAGE: 
      return (() => {
        const { message, dialogId } = payload

        let dialogInd = -1;
        let i = -1;
        while(++i < state.dialogs.length) {
          if(state.dialogs[i].dialog['_id'] === dialogId) {
            dialogInd = i;
            break;
          }
        }

        state.dialogs[dialogInd].messages.push(message);

        if(dialogId === state.choosenDialogId) {
          state.readyMessengesData = testFormatMessages(state.dialogs[dialogInd], state.user)
          if(message.author !== state.user['_id']) {
            state.isLiveUnreaded = true
          }
        }
        
        state.v = getV();
        return state
      })()
    case INIT: 
      return (() => {
        let {dialogs, user} = payload;

        state = {...state, dialogs, user, isAuth: true}
        state.v = getV('init')

        return state
      })()
    case SET_DIALOG_TAB_MOD: // можно отделить
      return (() => {
        const { mod } = payload
        if(mod === 'search') {
          state.dialogTabMod = 'search'
          state.v = getV('searchPeople')
        } else if (mod === 'base') {
          state.dialogTabMod = 'base'
          state.v = getV()
        }
        return state
      })()
    case SET_SEARCH_PEOPLE:
      return (() => {
        const { data } = payload
        
        state.searchPeople = data
        state.showSearchPeople = data
        state.v = getV()

        return state
      })()
    case ADD_QUERY:
      return (() => {
        const { text } = payload
        state.showSearchPeople = state.searchPeople.filter(({nickName}) => {
          // Символ "a" одинаковый в английском и русском алфавитах
          const target = nickName.toLowerCase()
          const searchText = text.toLowerCase()
          const res = target.indexOf(searchText);
          return res !== -1;
        })

        return state
      })()
    case PICK_DIALOG:
      return (() => {
        const { _id, dialog } = payload
        
        // Вызов через поиск диалогов.
        if(dialog !== null) {
          state.dialogs.push({
            dialog, 
            messages: []
          })
        }

        state.choosenDialogId = _id;

        let dialogInd = findThatDialog(state.dialogs, _id);

        const readyMessengesData = testFormatMessages(state.dialogs[dialogInd], state.user)

        state.readyMessengesData = readyMessengesData;
        state.v = getV('pickDialog')

        return state
      })()
    case READ_MESSAGES: // moжно вынести как сокет евентер
      return (() => {
        const { pass } = payload
        // пусть подготовка переменных будет на нижнем уровне. 
        // приходится добавляеть переменные.. 
        state.readMessagesPass = pass
        state.v = getV('readMessages')
        return state
      })()
    case SET_READ_MESSAGES: 
      return (() => {
        const { dialogId, messagesId, pass } = payload;
        let dialogInd = findThatDialog(state.dialogs, dialogId);

        // Обновление данных в диалоге
        let isPartnerMessages = false

        let i = -1;
        while(++i < state.dialogs[dialogInd].messages.length) {
          let mi = -1;
          while(++mi < messagesId.length) {
            if(state.dialogs[dialogInd].messages[i]['_id'] === messagesId[mi]) {
              state.dialogs[dialogInd].messages[i].readed = true;
              if(state.dialogs[dialogInd].messages[i].author !== state.user['_id']) {
                isPartnerMessages = true
              }
            }
          }
          
        }

        if(isPartnerMessages) {
          state.isLiveUnreaded = false
        }

        // побочные действия
        const readyMessengesData = testFormatMessages(state.dialogs[dialogInd], state.user)
        
        state.readyMessengesData = readyMessengesData;
        state.v = getV(pass)
        return state
      })()
    case GET_POLL_MESSAGES:
      return (() => {
        state.v = getV('getPollMessages')
        return state
      })() 
    case SET_V: 
      return (() => {
        const { pass } = payload;

        state.v = getV(pass)
        return state
      })()
    case SET_POLL_MESSAGES:
      return (() => {
        const { messages } = payload
        const dialogInd = findThatDialog(state.dialogs, state.choosenDialogId);

        state.dialogs[dialogInd].messages = [...messages, ...state.dialogs[dialogInd].messages];

        const readyMessengesData = testFormatMessages(state.dialogs[dialogInd], state.user)
        state.readyMessengesData = readyMessengesData;
        state.v = getV('setPollMessages')
        return state
      })()
    case EDIT_PROFILE: 
      return (() => {
        const {nickName, loginName, password} = payload

        state.newProfileData = {nickName, loginName, password, _id: state.user['_id']}

        state.v = getV('editProfile')
        return state
      })()
    case SET_MY_PROFILE: 
      return (() => {
        state.user = {...state.user, ...state.newProfileData}

        state.v = getV('setMyProfile')
        return state
      })()
    case SET_PROFILE: 
      return (() => {
        const {nickName, loginName, password, _id} = payload
        

        let i = -1;
        while(++i < state.dialogs.length) {
          const {author, partner} = state.dialogs[i].dialog
          if(author['_id'] === _id) {
            state.dialogs[i].dialog.author = {...state.dialogs[i].dialog.author, nickName, loginName, password}
            break
          }
          if(partner['_id'] === _id) {
            state.dialogs[i].dialog.partner = {...state.dialogs[i].dialog.partner, nickName, loginName, password}
            break
          }
        }

        state.v = getV('setProfile')
        return state
      })()

    case SET_ONLINE: 
      return (() => {
        const { _id, status } = payload

        let i = -1;
        while(++i < state.dialogs.length) {
          const {author, partner} = state.dialogs[i].dialog
          if(author['_id'] === _id) {
            state.dialogs[i].dialog.author.online = status
            break
          }
          if(partner['_id'] === _id) {
            state.dialogs[i].dialog.partner.online = status
            break
          }
        }

        state.v = getV('setOnline')
        return state
      })()
    case LOGOUT: 
      return (() => {
        
        const savedId = state.user['_id'];

        state = defaultStatus;
        state.logOutId = savedId
        state.v = getV('logOut')
        return state
      })()
    case UNSET_DIALOG_ID: 
      return (() => {
        state.choosenDialogId = null;

        state.v = getV()
        return state
      })()
    default: 
      return state;
  }
}

function getV(str='') { return `${str}$$${Math.random()}` }

function testFormatMessages({ messages: messagesPass }, user) {
  
  const messages = messagesPass
  .sort(({createdAt: aCT}, {createdAt: bCT}) => {
    if (new Date(aCT) > new Date(bCT)) {
      return 1
    } else {
      return -1
    }
  })

  const res = [];
  
  let lastDate = {mounth: null, day: null};

  let i = -1;
  while(++i < messages.length) {
    const { createdAt } = messages[i]
    const workDate = new Date(createdAt);
    const newLastDate = {
      mounth: workDate.getMonth(), 
      day: workDate.getDate()
    }

    if(
      (lastDate.mounth !== newLastDate.mounth) || 
      (lastDate.day !== newLastDate.day)
    ) {
      // новая пачка
      lastDate = newLastDate;
      res.push({
        date: getStringDate(newLastDate), 
        messages: [getEndpointMessage(messages[i], user)]
      })
    } else {
      // пуш сообщения в старую пачку
      res[res.length - 1].messages.push(
        getEndpointMessage(messages[i], user)
      )
    }

  }

    

  return res

  function getStringDate ({mounth, day}) {
    const mounthNames = {
      '0': 'January ',
      '1': 'February',
      '2': 'March',
      '3': 'April',
      '4': 'May',
      '5': 'June',
      '6': 'Jule',
      '7': 'August',
      '8': 'September',
      '9': 'October',
      '10': 'November',
      '11': 'December'
    }
    return `${day} ${mounthNames[mounth+'']}`
  }

  function getEndpointMessage(messages, user) {
    const { text, author, createdAt, readed, prevPoll } = messages
    const workDate = new Date(createdAt);
    const resTime = `${workDate.getHours()}:${workDate.getMinutes()}`
    const resObject = {
      time: resTime,
      text, 
      isMe: author === user['_id'],
      readed,
      prevPoll
    }
    return resObject
  }
  
}

function findThatDialog(dialogs, id) {
  let resInd = -1;

  let i = -1;
  while(++i < dialogs.length) {
    if(dialogs[i].dialog['_id'] === id) {
      resInd = i;
      break
    }
  }
  return resInd
}