import React from 'react'

import { Tag } from 'antd'

import './CheckTags.scss';

const { CheckableTag } = Tag;
const CheckTags = ({fValue, sValue, fHandl, sHandl, checkParam}) => {

  return (
    <div className='checkTags'>
      <CheckableTag 
        onClick={() => !checkParam && fHandl()}
        checked={checkParam}>
        {fValue}
      </CheckableTag>
      <CheckableTag
        onClick={() => checkParam && sHandl()}
        checked={!checkParam}>
        {sValue}
      </CheckableTag>
    </div>
  )
}

export default CheckTags