import * as types from '../constants/ActionTypes'
import store from '@/store'

export const microEvent = () => ({
  type: types.MICRO_EVENT,
  payload: Math.random()
})

export const addElement = (obj) => ({
  type: types.ADD_ELEMENT,
  payload: obj
})

export const privateMsg = (payload) => ({
  type: types.PRIVATE_MSG, 
  payload
})

export const joinToRoom = (payload) => ({
  type: types.JOIN_TO_ROOM,
  payload
})

export const getMsgs = () => ({
  type: types.GET_MSGS
})

export const pickDialog = (_id, dialog = null) => ({
  type: types.PICK_DIALOG,
  payload: { _id, dialog }
})

export const setDialogTabMod = (mod) => ({
  type: types.SET_DIALOG_TAB_MOD,
  payload: { mod }
})

export const setSearchPeople = (data) => ({
  type: types.SET_SEARCH_PEOPLE,
  payload: { data }
})

export const createNewDialog = (id = null) => ({
  type: types.CREATE_NEW_DIALOG,
  payload: { userId: id }
})

export const createMessage = (text) => ({
  type: types.CREATE_MESSAGE,
  payload: { text }
})

export const setMessage = (message, dialogId) => ({
  type: types.SET_MESSAGE,
  payload: { message, dialogId }
})

export const setDialog = (dialog) => ({
  type: types.SET_DIALOG,
  payload: { dialog }
})

export const joinRoom = userId => ({
  type: types.JOIN_ROOM,
  payload: { userId }
})

export const readMessages = (pass = '') => ({
  type: types.READ_MESSAGES,
  payload: { pass }
})

export const setReadMessages = (dialogId, messagesId, pass) => ({
  type: types.SET_READ_MESSAGES,
  payload: { dialogId, messagesId, pass }
})

export const getPollMessages = () => ({
  type: types.GET_POLL_MESSAGES
})

export const setV = (pass) => ({
  type: types.SET_V,
  payload: { pass }
})

export const setPollMessages = (messages) => ({
  type: types.SET_POLL_MESSAGES,
  payload: { messages }
})

export const editProfile = ({nickName, loginName, password}) => ({
  type: types.EDIT_PROFILE,
  payload: {nickName, loginName, password}
})

export const setProfile = ({nickName, loginName, password, _id}) => ({
  type: types.SET_PROFILE,
  payload: {nickName, loginName, password, _id}
})

export const setMyProfile = () => ({ // служебка. Нужно отделять такие
  type: types.SET_MY_PROFILE
})

export const setOnline = (_id, status) => ({
  type: types.SET_ONLINE,
  payload: { _id, status }
})

export const logOut = () => ({
  type: types.LOGOUT
})

export const addQuery = (text) => ({
  type: types.ADD_QUERY,
  payload: { text }
})

export const unsetDialogId = () => ({
  type: types.UNSET_DIALOG_ID
})


export {createUser, loginUser, checkAuth} from './network'