import { Button } from 'antd'
import React from 'react'
import { connect } from 'react-redux'
import { useHistory, useLocation } from 'react-router-dom'

import './Navigation.scss'

export const Navigation = (props) => {

  const history = useHistory();
  const location = useLocation();
  const buttons = [
    {text: 'Market', linkTo: 'market', disabled: true},
    {text: 'Messenger', linkTo: 'messenger', disabled: false},
    {text: ' ', linkTo: '', disabled: true},
    {text: 'Me', linkTo: 'me', disabled: false},
  ]

  return (
    <section className='navigation'>
      <div className='navigation__wrapper'>
        {buttons.map(({text, linkTo, disabled}) => {
          return <div className='navigation__wrapper_button'>
          <Button onClick={() => history.push(linkTo)}
            disabled={disabled}>
            {text}
          </Button>
        </div>
        })}
      </div>
    </section>
  )
}

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(Navigation)
