import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { Route, Switch, useLocation } from 'react-router-dom'
import { connect } from 'react-redux'

import { Target } from '@@/components'

import { addElement } from '@/actions';

import './ChooseBoard.scss'

const ChooseBoard = (
  {
    productsEvents = {}, 
    rightPass = false, 
    link = false, 
    
    addElement
  }) => {

  const products = productsEvents?.products || null
  if(!rightPass) {
    rightPass = products
  }

  const [routes, setRoutes] = useState(null)

  // let str = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent lobortis quis libero non luctus.'

  // const reg = new RegExp('[.]|[,]', 'g')

  // const data = str.replace(reg, '').split(' ')
  const { pathname } = useLocation();

  const getLastElem = path => {let ar = path.split('/'); return ar[ar.length-1]}


  if(routes === null) {
    let res = rightPass.payload.map(({name, type}, i) => {
      return type === 'dir' 
      ? <Route path={`${pathname}/${name}`}>
        <ChooseBoard rightPass={rightPass.payload[i]} link={name} addElement={addElement}/>
      </Route>
      : null
    });

    setRoutes(res);
  }

  return (
    <>
      { rightPass.name === getLastElem(pathname) ?
          <div className={'chooseBoard'}>
            {rightPass.payload.map(({name, type, payload}) => {
              return <Target text={name} type={type} onClick={() => addElement({name, ...payload})} />
            })}
          </div>
        : null
      }
      
      <Switch>
        {routes}
      </Switch>
    </>
  )
}

ChooseBoard.propTypes = {

}

const mapState = ({products}, {link}) => {
  debugger
  return {
    productsEvents: !link ? products.productsEvents : {products: false}
  }
}

export default connect(mapState, {addElement})(ChooseBoard);
