import React from 'react'
import PropTypes from 'prop-types'
import { useLocation, Link } from 'react-router-dom'

import './Bread.scss'

const Bread = props => {

  const {pathname} = useLocation();

  const paths = pathname.split('/').splice(1)
  
  function preparePaths(arr = paths) {
    const res = [];
    let i = -1;

    let sumPath = ''

    while(++i < arr.length) {
      let pathDls = `/${arr[i]}`;
      res.push({text: arr[i], path: sumPath+pathDls});

      sumPath+=pathDls;
    }

    return res
  }

  const readyPaths = preparePaths()
  debugger
  return (
    <div className={'bread'}>
      {readyPaths.map(({path, text}) => 
      <li className={'bread_links'}>
        <Link to={path}>{text}</Link>
      </li>)}
    </div>
  )
}

Bread.propTypes = {

}

export default Bread
