import { auth } from '@/core/api';
import { openNotification, upFirstSimbol } from '@/utils'
import * as types from '@/constants/ActionTypes';
 
const dataInit = payload => ({
  type: types.INIT,
  payload
})

const effecter = ({status, token, data, message}, isTokenLogin = false) => dispatch => {
  if(status === 'success') {
    localStorage.token = token;
    window.axios.defaults.headers.common['token'] = token;
    openNotification({type: status, title: upFirstSimbol(status), description: message});
    dispatch(dataInit(data))
  } else {
    openNotification({type: status, title: upFirstSimbol(status), description: message})
    isTokenLogin && delete localStorage.token
  }
}

export const loginUser = data => dispatch => {
  auth.login(data).then(({data}) => {
    console.log('%c%s', 'font-size: 22px; color: navy;', 'SERVER_RES')
    console.log('RES:', data);

    effecter(data)(dispatch)
  }).catch(er => {
    console.log('%c%s', 'color: red;font-size:22px;', 'AXIOS_LOGIN_ERR:', er)
  })
}

export const createUser = data => dispatch => {
  auth.register(data).then(({data}) => {
    console.log('%c%s', 'font-size: 22px; color: navy;', 'SERVER_RES')
    console.log('RES_DEST:', data);

    effecter(data)(dispatch)
  }).catch(er => {
    console.log('%c%s', 'color: red;font-size:22px;', 'AXIOS_REGISTER_ERR:', er)
  })
}

export const checkAuth = () => dispatch => {
  auth.checkToken().then(({data}) => {
    
    console.log('%c%s', 'font-size: 22px; color: navy;', 'SERVER_RES')
    console.log('RES_CHECK:', data);

    effecter(data, true)(dispatch)
  })
  .catch(er => {
    console.log('%c%s', 'color: red;font-size:22px;', 'AXIOS_AUTO_LOGIN_ERR:', er)
  })
}

/* 
Имееться "работающая" авторизация на сервере.. данные доходят, но дальще
не обрабатываются. JWT функционирует нормально. Необходимо обновлять Axios.headers
в ручную, а иначе он поднимает старый токен.

Логин так же успешно работает и доставляет необходимую информацию.

*/