import React from 'react'
import classNames from 'classnames'

import './Message.scss'

const Message = ({text, time, isMe = true, readed = false}) => {

  const styleEffect = isMe?'-isMe':'-isPartner'
  return (
    <section className={classNames('message', `message${styleEffect}`)}>
      <div className={classNames('message__cloud', `message__cloud${styleEffect}`)}>
        <div className={'message__cloud_payload'}>
          {text}
        </div>
        <div className={classNames('message__cloud_time', `message__cloud_time${styleEffect}`)}>
          {time}
        </div>
      </div>
      {(isMe && !readed) ? <div className={'message__unreaded'} >{''}</div> : null}
    </section>
  )
}


export default Message