import React from 'react'
import classNames from 'classnames'

import { TextReducer } from '@@/containers'

import './Dialog.scss';

const  DialogView = (
  {
    imgPlug, 
    name, 
    messange, 
    infoCount, 
    online,
    messageDls,
    authorIsYou,
    isSearchPeopleMod = false,
    isChoosen,

    clickHandler
  }) => {
  return (
    <section className={classNames(
      'dialog', isChoosen 
      ? 'dialog__isChoosen' : null
    )} 
      onClick={clickHandler}>
      <div className='dialog__leftPart'>
        <div className='dialog__leftPart_imgPlug'>{imgPlug}</div>
        {online && <div className='dialog__leftPart_online'>{''}</div>}
      </div>
      <div className='dialog__rightPart'>
        <div className='dialog__rightPart_name'>
          {name}
        </div>
        {!isSearchPeopleMod
          ? <div className='dialog__rightPart_message'>
              <div className='dialog__rightPart_message-payload'>
                <TextReducer text={`${authorIsYou?'You: ':''}${messange}`} />
              </div>
              <div className='infoCountWrapper'>
                <div className={`dialog__rightPart_message${messageDls}`}>
                  {infoCount}
                </div>
              </div>
            </div>
          : null
        }
        
      </div>
    </section>
  )
}

export default DialogView
