import { io } from 'socket.io-client';
import { createStore, applyMiddleware, compose } from 'redux'
import { useHistory } from 'react-router-dom'

import { createLogger } from 'redux-logger'
import thunk from 'redux-thunk'
import reducer from './reducers'

import { openNotification } from '@/utils'

import { 
  setSearchPeople, 
  pickDialog, 
  setMessage, 
  setDialog,
  readMessages,
  setReadMessages,
  setV,
  setPollMessages,
  setProfile, 
  setMyProfile,
  setOnline,
  
} from '@/actions'

const middleware = [ thunk ];
if (process.env.NODE_ENV !== 'production') {
  middleware.push(createLogger());
}

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(
  reducer,
  composeEnhancers(applyMiddleware(...middleware))
)
// 'http://localhost:3000'
const socket = io(window.location.origin, {transports: ['websocket']}); 

socket
  .on('connect', (ev) => {
    socket.emit('MSG', {data: 'pass', token: "123"})
  })
  .on('CLIENT_MSG', (pass) => {
    console.log('%c%s', 'font-size: 22px; color: red;', pass)
  })
  .on('RES_PRIVATE_MSG', (pass) => {
    console.log('%c%s', 'font-size: 22px; color: navy;', 'PRIVATE_MSG');
    console.log('Message:', pass);

    
    // cb()
  })
  .on('RES_MSGS', data =>  {
    console.log('%c%s', 'font-size: 22px; color: aqua;', 'GET_MSGS');
    console.log('Data:', data)
  })
  .on('RES_PEOPLE', data => {
    store.dispatch(setSearchPeople(data))
  })
  .on('RES_NEW_DIALOG', ({dialog, target}) => {
    if(target === 'me') {
      store.dispatch(pickDialog(dialog['_id'], dialog))
    } else {
      store.dispatch(setDialog(dialog))
    }
    
  })
  .on('RES_MESSAGE', ({message, dialogId}) => {
    store.dispatch(setMessage(message, dialogId))
  })
  .on('SET_READ_MESSAGE', ({dialogId, messagesId, pass }) => {
    store.dispatch(setReadMessages(dialogId, messagesId, pass))
  })
  .on('RES_POLL_MESSAGES', ({messages}) => {
    store.dispatch(setPollMessages(messages))
  })

  .on('BAD_EDIT_PROFILE', ({message}) => {
    openNotification({type: 'error', title: 'Error', description: message})
  })
  .on('SET_PROFILE', ({newProfileData}) => {
    store.dispatch(setProfile(newProfileData))
  })
  .on('SET_MY_PROFILE', () => {
    store.dispatch(setMyProfile())
  })

  .on('SET_ONLINE', ({_id, status}) => {
    store.dispatch(setOnline(_id, status))
  })
  
  .on('disconnect', (reason) => {
    console.log('%c%s', 'font-size: 22px; color: crimson;', 'DISCON:')
    console.log('Reason:',reason);
  })


let lastV = '';

store.subscribe(() => {
  const freshState = store.getState();
  const {
    lastMessage: {addres, message}, 
    room,  
    v,
    newDialogTargetId,
    messageData,
    dialogs,
    choosenDialogId,
    user,
    readyMessengesData,
    readMessagesPass,
    newProfileData,
    logOutId
  } = freshState.dialogs;

  const token = localStorage.token;

  // какого черта это было... Почему он проходил?
  if(lastV !== v) {
    
    let checkSimbol = v.split('$$')[0];
    
    switch(checkSimbol) {
      case 'r': 
        (() => {
          
          socket.emit('JOIN_ROOM', { token, room }, (data) => {
            console.log('%c%s', 'font-size: 22px; color: green;', 'SUCCESS_JOIN');
            console.log('Rooms:', data);
          })

        })();
        break;
      case 'm': 
        (() => {
         
          socket.emit('PRIVATE_MSG', {
            token,
            payload: { addres, message }
          }, (lastRooms) => {
            console.log('%c%s', 'font-size: 22px; color: green;', 'SUCCESS_SEND_MSG');
            console.log('%c%s', 'font-size: 22px; color: aqua;', 'LAST_ROOMS:', lastRooms)
          })
          
        })();
        break;
      case 'g':
        (() => {
          console.log('SUB')
          socket.emit('GET_MSGS', {
            token
          });
        })()
        break;
      case 'init':
        (() => {
          socket.emit('JOIN', { token }, (rooms) => {
            console.log('%c%s', 'font-size: 22px; color: green;', 'SUCCESS_JOIN', rooms);
          })
        })();
        break
      case 'searchPeople':
        (() => {
          socket.emit('GET_PEOPLE', {
            token
          })
        })()
        break
      // case 'joinRoom':
      //   debugger
      //   (() => {
      //     socket.emit('JOIN_ROOM', {
      //       token, room: newDialogTargetId,
      //     })
      //   })()
      //   break
      case 'createDialog': 
        (() => {
          socket.emit('CREATE_NEW_DIALOG', {
            token,
            newDialogTargetId
          })
          
        })()
        break
      case 'createMessage': 
        (() => {
          socket.emit('CREATE_MESSAGE', {...messageData, token})
        })()
        break
      case 'readMessages':  // просто исполняемый код.. без проверок.. 
        (() => {
          
          let dialogInd = -1;
          while(++dialogInd < dialogs.length) {
            if(dialogs[dialogInd].dialog['_id'] === choosenDialogId) {
              break
            }
          }

          

          const targetUserId = dialogs[dialogInd].dialog.partner['_id'] === user['_id']
          ? dialogs[dialogInd].dialog.author['_id']
          : dialogs[dialogInd].dialog.partner['_id']
          // можно оптимизировать через реверс

          let partnerUnreadedMessagesIds = dialogs[dialogInd].messages
          .filter(({readed, author}) => !readed && author === targetUserId).map(({_id}) => _id)

          if(partnerUnreadedMessagesIds.length) {
            socket.emit('READ_MESSAGE', { token, 
              dialogId: choosenDialogId, 
              messagesId: partnerUnreadedMessagesIds,
              targetUserId,
              pass: readMessagesPass,
            })
          } else {
            store.dispatch(setV('pickEvent'))
          }
        })()
        break
      case 'getPollMessages':
        (() => {
          const poll = readyMessengesData['0'].messages['0'].prevPoll
          
          socket.emit('GET_POLL_MESSAGES', { poll, token })
        })()
        break
      case 'editProfile': 
        (() => {
          socket.emit('EDIT_PROFILE', { token, newProfileData })
        })()
        break
      case 'logOut': 
        (() => {
          socket.emit('LOGOUT', {_id: logOutId}, () => {
            
          })
        })()
      default:
        break
    }

    switch(checkSimbol) {
      case 'pickDialog':
        (() => {
          store.dispatch(readMessages('pickEvent'))
        })()
        break
      default:
        break
    }
  }

})
  

export default store