import React, { useState, useRef, useEffect } from 'react'
import { connect } from 'react-redux'
import classNames from 'classnames'


import { Message, DateElement } from '@@/components'

import { readMessages, getPollMessages } from '@/actions'

import './Chat.scss'



export const Chat = (
  {
  choosenDialogId,
  readyMessengesData,
  dialogs, 
  newDialogTargetId,
  v,
  isLiveUnreaded,

  readMessages,
  getPollMessages
}) => {
  

  const [overflowData, setOverflowData] = useState({})

  const overflow = useRef(null)

  const dialogIsPicked = choosenDialogId !== null

  const messages = (() => {
    const res = [];
    readyMessengesData
    .forEach(({date, messages}) => {
      res.push( <DateElement date={date} />);
      messages
      .forEach(data => {
        res.push( <Message {...data} />)
      })
    });
    return res;
  })()

  useEffect(() => {
    const { scrollHeight, scrollTop, clientHeight } = overflow.current

    const keyWord = v.split('$$')[0] 
    if(keyWord === 'pickEvent') {
      // Прокручивание скролл бара вниз при загрузке странцы
      overflow.current.scrollTop = scrollHeight - clientHeight;
    }
    if(keyWord === 'setPollMessages') {
      // поиск старой высоты и фиксация на ней. Эффект удлинения страницы вверх
      overflow.current.scrollTop = scrollHeight-overflowData.realHeight
    }

    if(keyWord === 'pickEvent' && (messages.length < 15 && messages.length >= 1)) {
      // сохранение старой высоты и запрос за новыми сообщения -> новая высота
      setOverflowData({realHeight: scrollHeight})
      getPollMessages()
    }
  })

  function mouseMoveHandler(ev) {
    if(isLiveUnreaded) {
      readMessages()
    }
  }

  function scrollHandler() {
    // обработка событие полнятия скрола на макс высоту
    const { scrollTop, scrollHeight } = overflow.current;
    if(scrollTop === 0 && (() => {
      // Если высота сткрола действительно = 0 и есть превПулл
      const prevPoll = readyMessengesData?.[0]?.messages?.[0]?.prevPoll || 'none'
      return prevPoll !== 'none'
    })()) {
      // То сохраняется высота и делается запрос
      setOverflowData({realHeight: scrollHeight})
      getPollMessages()
      
    }
  }

  
  
  return (
    <section className='chat' onMouseMove={mouseMoveHandler} >
      <div className='chat__main' ref={overflow} onScroll={scrollHandler}>
        <div className={classNames(
          'chat__main_wrapper', 
          !dialogIsPicked ? 'chat__main_wrapper-help' : null
          )}>
          { dialogIsPicked
           ? messages // [...messages, ...messages, ...messages] 
           : 'Pick dialog'
          }
        </div>
      </div>
    </section>
  )
}

const mapStateToProps = (
  {dialogs: 
    {
      choosenDialogId, 
      readyMessengesData, 
      dialogs, 
      newDialogTargetId,
      v,
      isLiveUnreaded,
     
    }}) => ({
  choosenDialogId, 
  readyMessengesData,
  dialogs, 
  newDialogTargetId,
  v,
  isLiveUnreaded,

})

const mapDispatchToProps = {
  readMessages, getPollMessages
}

export default connect(mapStateToProps, mapDispatchToProps)(Chat)
