import React, { useState } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'

import { privateMsg, joinToRoom, getMsgs } from '@/actions'

import './TestPage.scss';

export const TestPage = (
  { isAuth,

    privateMsg, 
    joinToRoom, 
    getMsgs 
  }) => {

  const [msg, setMsg] = useState('Hey, Zues');
  const [addres, setAddres] = useState('Olimpia');

  function sendHandler() {
    privateMsg({msg, addres})
  }

  const [room, setRoom] = useState('Olimpia');

  function joinHandler() {
    joinToRoom({room})
  }

  return (
    <>
      <section className='testPage'>
        <div className='testPage_msg'>
          <input value={msg} onChange={(ev) => setMsg(ev.target.value)} placeholder={'Msg'}/>
          <input value={addres} onChange={(ev) => setAddres(ev.target.value)} placeholder={'Addres'} />
          <button onClick={sendHandler}>SEND</button>
        </div>
        <div className='testPage_msg'>
          <input value={room} onChange={(ev) => setRoom(ev.target.value)} placeholder={'Room'} />
          <button onClick={joinHandler}>JOIN</button>
        </div>
        <div>
          <button onClick={() => {console.clear(); getMsgs()}}>GET_MSGS</button>
        </div>
      </section>
      {!isAuth && <Redirect to={'/login'} />}
    </>
  )
}

const mapStateToProps = ({dialogs:{isAuth}}) => ({
  isAuth
})

const mapDispatchToProps = {
  privateMsg, joinToRoom, getMsgs
}

export default connect(mapStateToProps, mapDispatchToProps)(TestPage)
