import React from 'react'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'

import { Button } from 'antd';
import { LeftOutlined } from '@ant-design/icons';

import './MeHeader.scss'

export const MeHeader = ({onEdit, setReset}) => {
  const history = useHistory();

  return (
    <section className='meHeader'>
      <div className='meHeader__leftPart' onClick={() => history.push('/')}>
        <LeftOutlined />
      </div>
      <div className='meHeader__rightPart'>
        <div className='meHeader__rightPart_label'>
          {`Profile${onEdit ? '*' : ''}`}
        </div>
        {
          onEdit &&
          <div className='meHeader__rightPart_reset'>
            <Button type="dashed" onClick={() => setReset(true)}>
              {'Reset changes'}
            </Button>
          </div>
        }
      </div>
    </section>
  )
}

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(MeHeader)
