import React, { useRef, useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'

import { ChatHeader, Chat, ChatInput } from './layout'

import './ChatPage.scss'

export const ChatPage = (
  {
    isAuth,

  }) => {
  const wrapper = useRef();
  const inputRef = useRef();
  const [inputHeight, setInputHeight] = useState(0)
  const [height, setHeight] = useState({payload: 0, seted: false})


  useEffect(() => {

    if(inputHeight === 0) {
      console.log(inputRef.current.clientHeight)
      setInputHeight(inputRef.current.clientHeight)
    }
    if(!height.seted && inputHeight !== 0) {
      
      setHeight({payload: wrapper.current.clientHeight, seted: true})
    }
    
    // возможна проблема с перезапись в объект window
    window.onresize = function () {
      
      // После ресайза необходимо занулить сообщения
      // Что бы точно знать высоту, которая имеется в распоряжении
      setHeight({payload: inputHeight, seted: false})

      // небольшая добавочка) можно сделать инпут в относительных единицах
      // тогда нулевой итерации можно было бы вытащить инфу и перерисовать
      // уже с высотой в пикселях
    }
  })

  return (
    <>
      <section className='chatPage'>
        <div className='chatPage_header'>
          <ChatHeader />
        </div>
        
        <div className='chatPage-wrapper' ref={wrapper}>
          <div className='chatPage_messages' style={{height: height.payload - inputHeight}}>
            <Chat />
          </div>

          <div className='chatPage_input' ref={inputRef}>
            <ChatInput />
          </div>
        </div>
        {!isAuth && <Redirect to={{pathname: "/login", state: { from: '/messenger'}}} />}
      </section>
    </>
  )
}

const mapStateToProps = ({dialogs: {isAuth}}) => ({
  isAuth
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(ChatPage)
