import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import classNames from 'classnames'
import { useHistory } from 'react-router-dom'

import { pickDialog, createNewDialog, unsetDialogId } from '@/actions'

import { Dialog, SearchPeople } from '@@/containers'

import './DialogsTab.scss'

export const DialogsTab = (
  {
    dialogs,
    user,
    dialogTabMod, 
    choosenDialogId,
    v,
    showSearchPeople,

    pickDialog,
    createNewDialog,
    unsetDialogId
  }) => {
  const history = useHistory();

  //====================MOBILE_ONLI====================
  useEffect(() => {
    if(choosenDialogId !== null) {
      unsetDialogId()
    }
  })
  //====================MOBILE_ONLI====================

  const clickHandler = _id => {
    history.push('chat')
    pickDialog(_id)
  }

  const dialogsComponents = dialogs.map(dialogData => 
    <Dialog {...dialogData} 
    me={user} 
    onClick={clickHandler} 
    choosenId={choosenDialogId} 
    />)

  const showDialogs = dialogsComponents.length;
  
  const searchPeopleClickHandler = (id) => {
    history.push('chat')
    // Этот код нужно вынести на сервер, если я буду пуллить диалоги
    let dialogId = '';
    if(dialogs.some(({dialog: {author, partner, _id}}) => {
      if(author['_id'] === id || partner['_id'] === id) {
        dialogId = _id;
        return true
      }
    })) {
      pickDialog(dialogId)
    } else {
      createNewDialog(id)
    }
    
  }
  const choosenUserId = (() => {
    let res = null
    let i = -1;
    while(++i < dialogs.length) {
      const { dialog: {author, partner, _id} } = dialogs[i];
      if(_id === choosenDialogId) {
        if(author['_id'] === user['_id']) {
          res = partner['_id']
        } else {
          res = author['_id']
        }
      }
    }

    return res;
  })()

  const searchPeoples = showSearchPeople.map(searchUser => {
    return searchUser['_id'] !== user['_id']
    ? 
    <SearchPeople user={searchUser} 
    onClick={searchPeopleClickHandler} 
    choosenUserId={choosenUserId} 
    />
    : null
  })

  console.log('DIALOGS_COMPONENT:', dialogsComponents)

  return (
    <section className='dialogsTab'>
      {
        dialogTabMod === 'search'
        ?
        <div className='dialogsTab__wrapper'>
          {searchPeoples}
        </div>
        :
        <div className={classNames(
          'dialogsTab__wrapper', 
          showDialogs ? null : 'dialogsTab__wrapper-help',
        )}>
          {showDialogs
            ? dialogsComponents
            : "Find some-body"
          } 
        </div>
      }
      
    </section>
  )
}

const mapStateToProps = (
  {dialogs: {
    dialogs, 
    user, 
    dialogTabMod, 
    choosenDialogId,
    v,
    showSearchPeople,
  }
  }) => ({
  dialogs, 
  user, 
  dialogTabMod, 
  choosenDialogId,
  v,
  showSearchPeople,
})

const mapDispatchToProps = {
  pickDialog, 
  createNewDialog,
  unsetDialogId
}

export default connect(mapStateToProps, mapDispatchToProps)(DialogsTab)
