import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import classNames from 'classnames'
import { useHistory, useLocation } from 'react-router-dom'

import { createUser, loginUser, checkAuth } from '@/actions'

import { Button, Input, message } from 'antd'

import { CheckTags } from '@@/components'

import './Login.scss';

export const LoginPage = (
  { isAuth,

    createUser, 
    loginUser, 
    checkAuth 
  }) => {
  const history = useHistory();
  const location = useLocation();

  const [workPlace, setWorkPlace] = useState('create');
  const [data, setData] = useState({nickName: '', password: '', passwordConfirm: ''})
  const { nickName, password, passwordConfirm } = data;

  useEffect(() => {
    
    localStorage?.token && !isAuth && checkAuth();
    isAuth && localStorage?.token && (() => {
      
      const { from } = location.state || { from: {pathname: "/messenger"} }
      history.replace(from)
    })();

    return () => {

    }
  })


  function sumbitHandl(ev) {
    ev.preventDefault();
    let errors = false;

    if(password.length < 3) {
      errors = true
      message.error('Password too short', 3);
    }
    if(nickName.length < 3) {
      errors = true
      message.error('Nickname too short', 3);
    }

    if(workPlace === 'create') {
      if(passwordConfirm !== password) {
        errors = true;
        message.error('Passwords are not equal', 3);
      }
      !errors && createUser(data);
    } else {
      !errors && loginUser(data);
    }
  }

  const chechProps = {
    fValue: 'LOGIN',
    sValue: 'CREATE NEW',
    fHandl: () => setWorkPlace('login'),
    sHandl: () => setWorkPlace('create'),
    checkParam: workPlace === 'login'
  }

  return (
    <div className='login'>
      <div className='login__checkTags'>
        <CheckTags {...chechProps} />
      </div>
      <div className='login__form'>
        <form onSubmit={sumbitHandl}>
          <div className={classNames('formElement')}>
            <Input 
              placeholder={'Nickname'}
              onChange={ev => setData({...data, nickName: ev.target.value})}
              value={nickName}/>
          </div>
          <div className={classNames('formElement')}>
            <Input.Password 
              placeholder={'Password'}
              onChange={ev => setData({...data, password: ev.target.value})}
              value={password}/>
          </div>
          {
            workPlace === 'create' &&
            <div className={classNames('formElement')}>
              <Input.Password 
                placeholder={'Password confirm'}
                onChange={ev => setData({...data, passwordConfirm: ev.target.value})}
                value={passwordConfirm}/>
            </div>
          }
          <div className={classNames('formElement', 'login__form_submit')}>
            <Button onClick={ev => console.log('click')} htmlType="submit">
              {"SEND DATA"}
            </Button>
          </div>
        </form>
      </div>
    </div>
  )
}

const mapStateToProps = ({dialogs: {isAuth}}) => ({
  isAuth
})

const mapDispatchToProps = {
  createUser, loginUser, checkAuth
}

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage)
