import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'

import { Search } from '@@/components'
import { ChooseBoard } from '@@/containers'

import './Main.scss';

const CPMain = ({microEvent}) => {
  return (
    <section className={'cPMain'}>
      <div className={'cPMain_leftSpace'} />
      <div className={'cPMain_main'}>
        <Search />
        <ChooseBoard />
        {/* <button onClick={() => microEvent()}>
        {'ChoosePage'}
        </button>
        <Link to='/result'>
          {'toResult'}
        </Link> */}
      </div>
      <div className={'cPMain_rightSpace'} />
    </section>
  )
}

CPMain.propTypes = {

}

export default CPMain
