import React, { useState, useEffect, useRef } from 'react'
import { connect } from 'react-redux'
import { Redirect, useLocation } from 'react-router-dom'

import { Editor, MeHeader } from './layout'

import './MePage.scss'

export const MePage = (
  {
    isAuth
  }) => {
  const location = useLocation()
  const [onEdit, setOnEdit] = useState(false)
  const [reset, setReset] = useState(false); // служебная вызывалка
  // Нужно начинать описывать через архитект. докум.

  const [height, setHeight] = useState({payload: 0, seted: false});
  const wrapper = useRef();

  useEffect(() => {
    if(!height.seted) {
      setHeight({payload: wrapper.current.clientHeight, seted: true})
    }

    window.onresize = function () {
      setHeight({payload: 0, seted: false})
    }
  })

  return (
    <>
      <section className='mePage'>
        <div className='mePage__header'>
          <MeHeader onEdit={onEdit} setReset={(pass) => setReset(pass)} />
        </div>
        <div className='mePage__wrapper' ref={wrapper}>
          <div className='mePage__wrapper_editor' 
            style={{height: height.payload}}>
            <Editor 
              reset={reset} 
              setReset={pass => setReset(pass)} 
              setOnEdit={(pass) => setOnEdit(pass)} 
              onEdit={onEdit} /> 
          </div>
        </div>
      </section>
      {!isAuth && <Redirect to={{pathname: "/login", state: { from: location.pathname}}} />}
    </>
  )
}

const mapStateToProps = ({dialogs}) => ({
  isAuth: dialogs.isAuth
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(MePage)
