import React from 'react'
import { connect } from 'react-redux'

import View from '../view'

export const Dialog = (
  {
    dialog,
    messages,
    me,
    choosenId,

    onClick
  }) => {
  
  // Все это.. можно и нужно перенести в редюсер
  let showData;
  const showTarget = dialog.author['_id'] === me['_id']
  ? dialog.partner : dialog.author
  
  const isChoosen = dialog['_id'] === choosenId;

  if(messages.length) {

    const readed = messages[messages.length-1].readed;
    const authorIsYou = messages[messages.length-1].author === me['_id'];

    function getMessageDls() {
      const numbReaded = +readed,
      numbAuthorIsYou = +authorIsYou,
      chooseTree = {
        '0': {'0': '-infoCount', '1': '-none'},
        '1': {'0': '-unreaded', '1': '-none'}
      }
      return chooseTree[numbAuthorIsYou][numbReaded]
    }
    const messageDls = getMessageDls();
    
    function getUnreadedCount() {
      
      const revMessages = messages.reverse();

      let resCount = 0
      let deathFlag = false;
      while(!deathFlag && (resCount < revMessages.length)) {
        if(!revMessages[resCount].readed) {
          resCount++;
        } else {
          deathFlag = true
        }
      }
      if(resCount >= 10) {
        return '9+'
      } else return resCount;
    }

    showData = {
      imgPlug: showTarget.nickName[0],
      name: showTarget.nickName,
      messange: messages[messages.length-1].text,
      infoCount: messageDls === '-infoCount' ? getUnreadedCount() : null,
      online: showTarget.online,
      authorIsYou, 
      messageDls,
      isChoosen
    }

  } else {
    showData = {
      imgPlug: showTarget.nickName[0],
      name: showTarget.nickName,
      messange: '',
      infoCount: null,
      online: showTarget.online,
      authorIsYou: false,
      messageDls: '-none',
      isChoosen
    }
  }

  

  return (
    <View {...showData} 
      clickHandler={() => !isChoosen && onClick(dialog['_id'])} 
      />
  )
}

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(Dialog)
