import React, { useState } from 'react'
import { connect } from 'react-redux'

import { Input } from 'antd'
import { CloseOutlined, SearchOutlined } from '@ant-design/icons';

import { setDialogTabMod, addQuery } from '@/actions'

import './DialogsSearch.scss'

export const DialogsSearch = (
  {
    dialogTabMod,

    setDialogTabMod,
    addQuery
  }) => {

  const [searchValue, setSearchValue] = useState('')
  const isSearch = dialogTabMod === 'search'

  function focusHandler (ev) {
    if(!isSearch) {
      setDialogTabMod('search')
    }
  }

  function changeHandler (ev) {
    setSearchValue(ev.target.value)
    addQuery(ev.target.value)
  }

  function closeClickHandler(ev) {
    setDialogTabMod('base')
    setSearchValue('')
  }

  

  return (
    <section className={'dialogsSearch'}>
      <Input
       placeholder={'Search persons'} 
       bordered={false}  
       onFocus={focusHandler}
       onChange={changeHandler}
       value={searchValue}
       onPressEnter={ev => console.log('PRESS_ENTER')}

       />

      <div className={'dialogsSearch__stopSearch'}>
        {
          isSearch 
          ? <div 
              className={'dialogsSearch__stopSearch_icon'} 
              onClick={closeClickHandler}>
              <CloseOutlined />
            </div>
          : <div 
              className={'dialogsSearch__stopSearch_icon'} 
              onClick={focusHandler}>
              <SearchOutlined />
            </div>
        }
      </div>
    </section>
  )
}

const mapStateToProps = ({dialogs: {dialogTabMod}}) => ({
  dialogTabMod
})

const mapDispatchToProps = {
  setDialogTabMod,
  addQuery
}

export default connect(mapStateToProps, mapDispatchToProps)(DialogsSearch)
