import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'

import { Button, Input, message } from 'antd'

import { editProfile, logOut } from '@/actions'
import { openNotification } from '@/utils'

import './Editor.scss'

export const Editor = (
  {
    user,

    onEdit,
    setOnEdit,
    reset,
    setReset,

    editProfile,
    logOut,
  }) => {
  const history = useHistory();
  const [inputValue, setInputValue] = useState({
    nickName: user?.nickName || '',
    loginName: user?.loginName || '',
    password: user?.password || '',
  });

  let unsavedParams = [];
    if(user !== null) {
      const { nickName='', loginName='', password=''} = user;
      const { nickName: nN, loginName: lN, password: pw } = inputValue;
      if(nickName!==nN) {
        unsavedParams.push('nickName')
      }
      if(loginName!==lN) {
        unsavedParams.push('loginName')
      }
      if(password !==pw) {
        unsavedParams.push('password')
      }
    }

  if(unsavedParams.length && !onEdit) {
    setOnEdit(true)
  } else if(!unsavedParams.length && onEdit) {
    setOnEdit(false)
  }
  

  const { nickName, loginName, password } = inputValue
  
  function resetHandler() {
    const { nickName, loginName, password } = user;
    setInputValue({nickName, loginName, password});
    setReset(false);
  }

  function saveHandler() {
    // Необходима минимальная валидация
    if(!unsavedParams.length) {
      return
    }
 
    if (Object.values(inputValue).every((el) => el.length >= 3)) {
      editProfile(inputValue)
    } else {
      openNotification({
        type: 'warning', 
        title: 'Warning', 
        description: 'The minimum response length for each field is 3 characters'
      })
    }
  }

  function logOutHandler() {
    delete localStorage.token
    delete window.axios.defaults.headers.common['token']
    logOut();
    history.push('login')
  }

  useEffect(() => {
    if(reset) {
      resetHandler()
    }
  })


  return (
    <section className='editor'>
      <div className='editor__main'>
        <div className='editor__main_inputs'>

          <div className='editor__main_inputs_wrapper'>
            <div className="addone">{"NickName"}</div>
            <Input 
              onChange={({target: {value}}) => setInputValue({...inputValue, nickName: value})}
              // addonBefore={}  
              value={nickName} />
          </div>

          <div className='editor__main_inputs_wrapper'>
            <div className="addone">{"LoginName"}</div>
            <Input 
              onChange={({target: {value}}) => setInputValue({...inputValue, loginName: value})}
              value={loginName} />
          </div>

          <div className='editor__main_inputs_wrapper'>
            <div className="addone">{"Password"}</div>
            <Input 
              onChange={({target: {value}}) => setInputValue({...inputValue, password: value})}
              // addonBefore={}
              placeholder={'New password'}
              value={password} />
          </div>

         
        </div>
        <div className='editor__main_buttons'>
          <Button onClick={saveHandler}>
            Save
          </Button>
          <Button danger onClick={logOutHandler}>
            LogOut
          </Button>
        </div>
      </div>
      {/* <div className='editor__state'>
        {
          unsavedParams.length 
          ?
          <>
            <div className='editor__state_reset'>
              <Button type="dashed" onClick={resetHandler}>
                {'Reset changes'}
              </Button>
            </div>
            <div className='editor__state_unsaved'>
              {'*Unsaved:'}
              <ul>
                {unsavedParams.map(el => <li>{el}</li>)}
              </ul>
            </div>
            
          </>
          : null
        }
      </div> */}
    </section>
  )
}

const mapStateToProps = ({dialogs: {user}}) => ({
  user
})

const mapDispatchToProps = {
  editProfile,
  logOut
}

export default connect(mapStateToProps, mapDispatchToProps)(Editor)
