import { combineReducers } from 'redux'
import {  MICRO_EVENT, ADD_ELEMENT } from '@/constants/ActionTypes'

const prepareTestData = () => {

  const workStr = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'

  const reg = new RegExp('[.]|[,]', 'g');

  const data = workStr.replace(reg, '').split(' ');

  let res = []

  function recurs(pass, path) {
    let name;
    if(pass) {
      name = pass
    } else name = data.splice(0, 1)[0];
    
    if(Math.random() > 0.5 && data.length) {
      let count = Math.floor(Math.min(Math.random()*5, data.length-1))+1;
      let elems = data.splice(0, count);

      path.push({ name, type:'dir',
        payload: []
      })
      
      elems.forEach(name => recurs(name, path[path.length-1].payload))

    } else {
      path.push({name, type: 'target', payload: {
        A: Math.random(),
        B: Math.random(),
        C: Math.random()
      }})
    }
  }

  while(data.length) {
    recurs(null, res)
  }

  let passData = {name: 'main', type: 'dir', payload: res};
  window.localStorage.testData = JSON.stringify(passData);
  return passData;
}


let parsed = window.localStorage?.testData ?
JSON.parse(window.localStorage?.testData) : prepareTestData() 

const defState = {
  products: parsed,
}

const productsEvents = (state = defState, {type, payload}) => {
  switch(type) {
    case MICRO_EVENT: 
      return (() => {
        return state+payload
      })()
    default: 
      return state;
  }
}


const microElsDefState = {
  elements: {}
}

const microEls = (state = microElsDefState, {type, payload}) => {
  switch(type) {
    case ADD_ELEMENT: 
      debugger
      return (() => {
        const {name, A, B, C} = payload;

        if(state.elements.hasOwnProperty(name)) {
          state.elements[name].count++;
        } else {
          state.elements[name] = {A, B, C, count: 1}
        }
        return state
      })();
    default: 
      return state
  }
}


export default combineReducers({
  productsEvents,
  microEls
})


